import React from 'react';
import DataTable from 'components/DataTable';
import Icon from 'components/Icon';
import { Button } from 'antd';

export default (self, employees) => [
  {
    title: '',
    name: 'deptName',
    tableItem: {},
    searchItem: {},
    formItem: {},
  },
  {
    title: '',
    name: 'distributionNetwork',
    dict: [
      {code: '0', codeName: ''},
      {code: '1', codeName: ''},
    ],
    tableItem: {},
    formItem: {
      type: 'select',
    }
  },
  {
    title: '',
    name: 'address',
    tableItem: {},
    formItem: {},
  },
  {
    title: '',
    name: 'type',
    tableItem: {},
    formItem: {}
  },
  {
    title: '',
    name: 'planBeginTime',
    tableItem: {},
    formItem: {
      type: 'datetime'
    }
  },
  {
    title: '',
    name: 'planEndTime',
    tableItem: {},
    formItem: {
      type: 'datetime'
    }
  },
  {
    title: '',
    name: 'workEmployee',
    tableItem: {
      render: (text) => text.map(item => item.title).join(',')
    },
    formItem: {
      type: 'transfer',
      modal: true,
      dataSource: employees,
      normalize: (value) => value.map(item => item.key)
    }
  },
  {
    title: '',
    name: 'content',
    formItem: {
      type: 'editor'
    }
  },
  {
    title: '',
    tableItem: {
      width: 180,
      render: (text, record) => (
        <DataTable.Oper>
          <Button tooltip="" onClick={self.onUpdate(record)}>
            <Icon type="edit" />
          </Button>
        <Button tooltip="" onClick={self.onDelete(record)}>
            <Icon type="trash" />
          </Button>
        </DataTable.Oper>
      )
    }
  }
];


